import React, { useState } from 'react'

export const Chahid = () => {
  const [open, setOpen] = useState({
    isOpen: false
  })
  const openModal = () => {
    setOpen({
      isOpen: true
    })
  }

  return (
    <div className='banner-one__content'>
      <a href='/category/video' className='thm-btn banner-one__btn'>
        <span>شاهد</span>
      </a>
    </div>
  )
}
