import React from 'react'
import { Heading } from 'theme-ui'
import Navigation from '@components/Navigation'
import useSiteMetadata from '@helpers-blog/useSiteMetadata'
import attachSocialIcons from '@helpers/attachSocialIcons'
import Divider from '@components/Divider'

const styles = {
  social: {
    mb: [3, 0]
  },
  navHeader: {
    display: [`none`, `block`]
  }
}

export const FooterSocial = () => {
  const { social } = useSiteMetadata()

  return (
    <>
      <Heading variant='h4' as='p' sx={styles.navHeader}></Heading>
      <Navigation
        // items={attachSocialIcons(social)}
        variant={[`horizontal`, `vertical`]}
        wrapperStyle={styles.social}
      />

      <div className='footer-widget'>
        <Divider />
        <Divider />

        <div className='footer-widget__social'>
          <a href='https://twitter.com/Bilignar1'>
            <i className='fab fa-twitter'></i>
          </a>

          <a href='https://www.youtube.com/channel/UCkQfjGJo-lE_sBTp9al8w2g'>
            <i className='fab fa-youtube'></i>
          </a>
          <a href='mailto:contact@bilignar.com'>
            <i class='far fa-envelope'></i>
          </a>
        </div>
      </div>
    </>
  )
}
